import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import img1 from './images/001.jpg';
import img2 from './images/002.jpg';
import img3 from './images/003.jpg';

class Box extends React.Component {
  constructor(props){
    super(props);
    var w = 10,
        h = 10;
    this.state = {
      w: w,
      h: h,
      arr: new Array(w*h).fill(''),
      val: 'X',
      winarr: null
    }
    this.btnclick = this.btnclick.bind(this);
  }
  //
  //
  //i,  intvaldir
  geTctnuArr(i, intval, dir){
    var b = [],
        arr = this.state.arr,//
        w = this.state.w,//
        h = this.state.h;//
    var val = arr[i];
    //
    //lr
    //tb
    //false
    //i, 
    function isgb(i){
      var lr = (i + 1)%w,//01
          tb = Math.ceil((i + 1)/w);//1
      if((lr === 0) || (lr === 1)){//
        return 'lr';
      }else if((tb === 1) || (tb === h)){//
        return 'tb';
      }else{//
        return false;
      }
    }
    //
    function istrue(i){
      if(dir === 'lr'){
        return !(isgb(i) === 'lr');//
      }else if(dir === 'tb'){
        return !(isgb(i) === 'tb');//
      }else{
        return !(isgb(i));//
      }
    }
    //
    while(123){//
      //
      if(arr[i - intval] === val && istrue(i)){
        i -= intval;
      }else{
        break;
      }
    }
    while(123){//
      b[b.length] = i;
      //
      if((arr[i + intval] === val) && istrue(i)){
        i += intval;
      }else{
        break;
      }
    }
    return b;
  }
  //
  reset(){
    var w = this.state.w,//
        h = this.state.h;//
    this.setState({
      w: w,
      h: h,
      arr: new Array(w*h).fill(''),
      val: 'X',
      winarr: null
    })
  }
  btnclick(e){
    var arr = this.state.arr,
        index = parseInt(e.target.getAttribute('data-index'), 10),
        val = this.state.val;
    if(arr[index] || this.state.winarr){
      return false;
    };
    val = val === 'X' ? 'O' : val === 'O' ? 'X' : '';
    arr[index] = val;
    this.setState({
      arr: arr,
      val: val
    });
    this.isWin(index);
  }
  isWin(index){
    var w = this.state.w;//
    var winarr = this.geTctnuArr(index, 1, 'lr'); // 
    if ( !(winarr.length >= 5) ) {
      winarr = this.geTctnuArr(index, w, 'tb'); // 
    }
    if ( !(winarr.length >= 5) ) {
      winarr = this.geTctnuArr(index, (w - 1)); // 
    }
    if ( !(winarr.length >= 5) ) {
      winarr = this.geTctnuArr(index, (w + 1)); // 
    }
    if( winarr.length >= 5 ){
        this.setState({winarr});
      }
  }
  formatval ( val, rev ) {
    if ( rev ) { // 
      val = val === 'X' ? 'O' : val === 'O' ? 'X' : ''
    }
    if ( val === 'X' ) {
      return <img className="chess-img" alt="ll" src={img2}/>
    } else if ( val === 'O' ) {
      return <img className="chess-img" alt="xl" src={img3}/>
    } else {
      return <span></span>
    }
  }
  render() {
    var arr = this.state.arr,
        winarr = this.state.winarr,
        elearr = arr.map((txt, index) => {
          let valele = this.formatval(this.state.arr[index]),
              valcls = (winarr && (winarr.indexOf(index) >= 0)) ? 'winele' : '';
          return <div className={'grid-btn ' + valcls} data-index={index} onClick={this.btnclick} key={index}>{valele}</div>
        }),
        curele = null,
        curbox = null;
        if ( winarr ) {
          curele = this.formatval(this.state.val)
          curbox = <div>{curele}</div>
        } else if ( this.state.val ) {
          curele = this.formatval(this.state.val, true)
          curbox = <div>{curele}</div>
        }
    return (
      <div className="page-box">
        <img src={img1} alt="linling" className="linling"/>
        <div className="grid-body">
          <div className="player-ele">{curbox}</div>
          <div className="player-ele">
            <button className="reset-btn" onClick={() => {this.reset()}}></button>
          </div>
          <div className="grid-box" style={{width: ((this.state.w*30 - this.state.w) + 'px')}}>{elearr}</div>
        </div>
      </div>
    );
  }
}
ReactDOM.render(
  <Box/>,
  document.getElementById('root')
);



